import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent } from '../app/dashboard/dashboard.component';
import { TestsComponent } from '../app/tests/tests.component';
import { LoginComponent } from '../app/login/login.component';
import { RegisterComponent } from '../app/register/register.component';
import { ConstantsService } from '../app/services/constants.service';
import { AuthenticationGuardService } from '../app/services/authentication-guard.service';
import { CanDeactivateService } from '../app/services/can-deactivate.service';

const appRoutes: Routes = [
    { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
    {
        path: 'dashboard',
        component: DashboardComponent,
        canActivate: [AuthenticationGuardService],
        canDeactivate: [CanDeactivateService],
    },
    {
        path: 'tests',
        component: TestsComponent,
        canActivate: [AuthenticationGuardService],
        canDeactivate: [CanDeactivateService],
    },
    {
        path: 'login',
        component: LoginComponent,
    },
    {
        path: 'register',
        component: RegisterComponent,
    },
];
@NgModule({
    imports: [
        RouterModule.forRoot(appRoutes)
    ],
    exports: [
        RouterModule
    ]
})

/**
 * Main Routing module for the application.
 */
export class AppRoutingModule { }



