import { Injectable } from '@angular/core';
import { Role } from '../common/models/role';
@Injectable()
export class ConstantsService {
  public static Admin: Role = new Role("admin", 1);
  public static TG: Role = new Role("TestGenerator", 2);
  public static TT: Role = new Role("TestTaker", 3);

  public static APP_URL = '';
  public static TIMEOUT = 500;

  public static COOKIE_SESSION_ID = 'sessionId';
  public static COOKIE_USER_ROLE = 'userRole';
  public static COOKIE_PATH = '/';
  public static COOKIE_LOGIN_TOKEN = 'loginToken';
  public static COOKIE_ACCESS_TOKEN = 'accessToken';

  public static LOGIN_URL = 'http://localhost:4200/login';
  public static VERIFY_SESSION = '/users/verifysession/';

}



