import { Injectable, ViewContainerRef } from '@angular/core';
import { Subscription } from 'rxjs';
import { TimerObservable } from 'rxjs/observable/TimerObservable';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/timeout';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { CookieService } from 'ngx-cookie-service';
import { ConstantsService } from '../services/constants.service';
import { ObservableRequestService } from '../services/observable-request.service';

// import { SessionExpiredComponent } from 'app/shared/components/sessionexpired/session-expired.component';

/**
 * Service to logout the user when the Idle Time expires.
 */
@Injectable()
export class UserIdleService {
  private subscription: Subscription;
  viewContainerRef: ViewContainerRef;

  constructor(
    private cookieService: CookieService,
    private requestService: ObservableRequestService
  ) { }

  verifySessionValidity(sessionId: string): Observable<any> {
    return this.requestService
      .getRequest(ConstantsService.VERIFY_SESSION + sessionId);
  }

  showSessionExpiredPopup() {
    // Stop session verification timer
    this.subscription.unsubscribe();

    alert("Session Expired. Please Login Again.");
  }

  startSessionKeepAliveWatcher() {
    // 15 minutes on prod, 1 minute on dev
    const interval = (1000 * 60 * 5);

    const timer = TimerObservable.create(interval, interval);
    this.subscription = timer.subscribe(t => {
      const sessionId = this.cookieService.get(ConstantsService.COOKIE_SESSION_ID);

      this.verifySessionValidity(sessionId).subscribe(
        data => {
          if (data.flagResult) {
            return data.flagResult;
          } else {
            this.showSessionExpiredPopup();
          }
        },
        error => {
          if (error.status !== 0) {
            // Could be handy for debugging to determine why sessions timeout, please leave it here
            console.error(error); // tslint:disable-line

            // error status 0 means connection timeout
            this.showSessionExpiredPopup();
          }
        });
    });
  }
}



