import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subscriber } from 'rxjs/Subscriber';

import { CookieService } from 'ngx-cookie-service';
import { ConstantsService } from '../services/constants.service';
import { UserIdleService } from '../services/user-idle.service';
import { Role } from '../common/models/role';
// import { WarmUpService } from 'app/services/warmup/warmup.service';

@Injectable()
export class AuthenticationGuardService {
  isLoggedIn: Boolean;
  currentRole: Role;

  constructor(
    private userIdleService: UserIdleService,
    private cookieService: CookieService,
    private router: Router,
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    console.log("inside auth guard");
    return new Observable<boolean>(observer => {
      if (this.isLoggedIn) {
        // ALready logged in, don't perform verifySession, only admin check
        return this.verifyAdmin(observer, state);
      }

      const sessionId = this.cookieService.get(ConstantsService.COOKIE_SESSION_ID);

      if (sessionId === null || sessionId === '') {
        this.redirectToLogin(observer, `empty sessionId`);
      } else {
        this.userIdleService.verifySessionValidity(sessionId).subscribe(
          response => {
            if (response.flagResult && response.arrResult.status) {
              this.isLoggedIn = true;
              this.userIdleService.startSessionKeepAliveWatcher();
              this.verifyAdmin(observer, state);
            } else {
              this.redirectToLogin(observer, `false flagResult ${response.flagResult} - status ${response.arrResult.status}`);
            }
          },
          err => {
            this.redirectToLogin(observer, `session validity error ${err}`);
          });
      }
    });
  }

  verifyAdmin(observer: Subscriber<Boolean>, state: RouterStateSnapshot) {
    // Following is relevant only if user is logged in already.
    // If non-Admin user tries to access Admin pages, redirect to Dashboard.

    // if (!this.isAdminUser() && this.adminPageUrls.indexOf(state.url) >= 0) {
    //   this.router.navigate(['/dashboard']);
    //   observer.next(false);
    // } else {
    //   observer.next(true);
    // }

    observer.complete();
  }

  redirectToLogin(observer: Subscriber<Boolean>, reason: String) {
    console.log("not logged in");
    observer.next(false);
    observer.complete();
    window.location.replace(ConstantsService.LOGIN_URL);
  }

  isAdminUser(): boolean {
    const userRole = this.cookieService.get(ConstantsService.COOKIE_USER_ROLE);
    return userRole === ConstantsService.Admin.id.toString();
  }
}



