import { Component, OnInit } from '@angular/core';
import { SidebarMenu } from './sidebar-menu.model';
import { ConstantsService } from '../services/constants.service';
import { Observable } from 'rxjs/Observable';
import { AuthenticationGuardService } from '../services/authentication-guard.service';
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  menuItems: SidebarMenu[] = [];
  isLoggedIn: Boolean;                  // {1}

  constructor(private authService: AuthenticationGuardService) { }

  ngOnInit() {
    this.isLoggedIn = this.authService.isLoggedIn; // {2}

    if (this.isLoggedIn) {
      this.menuItems.push(new SidebarMenu("Dashboard", "dashboard", "/dashboard"));
      this.menuItems.push(new SidebarMenu("Tests", "list", "/tests"));
      this.menuItems.push(new SidebarMenu("Settings", "settings", "/settings"));
    } else {
      this.menuItems.push(new SidebarMenu("Login", "input", "/login"));
      this.menuItems.push(new SidebarMenu("Register", "face", "/register"));
    }

  }

}



